import React, { useState } from 'react';
import { Button } from 'ui/Button';
import TextField from 'ui/TextField';
import { validateName, validatePasswordMatch } from '../utils/validation';
import FormInput from './FormInput';
import PasswordField from '../../Login/components/PasswordField';

interface SignUpFormData {
  email: string;
  password: string;
  confirmPassword: string;
  name: string;
}

interface FormErrors {
  email: string;
  password: string;
  name: string;
}

const SignUpForm = () => {
  const [formData, setFormData] = useState<SignUpFormData>({
    email: '',
    password: '',
    confirmPassword: '',
    name: '',
  });

  const [errors, setErrors] = useState<FormErrors>({
    email: '',
    password: '',
    name: '',
  });

  /*    */
  const handleInputChange =
    (field: keyof SignUpFormData) =>
    (e: React.ChangeEvent<HTMLInputElement>) => {
      const value = e.target.value;
      setFormData((prev) => ({
        ...prev,
        [field]: value,
      }));
      if (field === 'name') {
        setErrors((prev) => ({ ...prev, name: validateName(value) }));
      }

      if (field === 'password' || field === 'confirmPassword') {
        setErrors((prev) => ({
          ...prev,
          password: validatePasswordMatch(
            field === 'password' ? value : formData.password,
            field === 'confirmPassword' ? value : formData.confirmPassword,
          ),
        }));
      }
    };

  return (
    <form className="hbp:w-[550px] hbp:gap-[75px] flex w-110 flex-col gap-15">
      {/* */}
      <div className="hbp:gap-[12.5px] flex w-full flex-col gap-[10px]">
        <label
          htmlFor="email"
          className="text-body-lg-semibold hbp:text-headline-2xl-semibold text-grey-600"
        >
           <span className="text-red-500">*</span>
        </label>
        <div className="hbp:gap-[12.5px] flex w-full flex-row items-center justify-center gap-[10px]">
          <TextField
            id="email"
            type="email"
            placeholder=" ."
            value={formData.email}
            onChange={handleInputChange('email')}
            className={`hbp:text-body-xl-regular aspect-[338/60] flex-1 ${errors.email ? 'border-red-500' : ''}`}
          />
          <Button
            text=""
            size="sm"
            className="hbp:w-[115px] hbp:h-[50px] hbp:text-body-xl-medium w-[92px] whitespace-nowrap"
          ></Button>
        </div>
        {errors.email && (
          <p className="mt-1 text-xs text-red-500">{errors.email}</p>
        )}
      </div>

      {/*   */}
      <div className="hbp:gap-1.5 flex flex-col gap-1">
        <label
          htmlFor="password"
          className="text-body-lg-semibold hbp:text-headline-2xl-semibold text-grey-600"
        >
           <span className="text-red-500">*</span>
        </label>
        <p className="hbp:mb-[7.5px] text-caption-sm-medium hbp:text-body-md-medium text-grey-300 mb-[6px]">
          , ,   8-16 .
        </p>
        <PasswordField
          value={formData.password}
          onChange={handleInputChange('password')}
        />

        {/*   */}
        <FormInput
          id="confirmPassword"
          type="password"
          placeholder=" ."
          value={formData.confirmPassword}
          onChange={handleInputChange('confirmPassword')}
          compareValue={formData.password}
          successMessage={
            formData.confirmPassword &&
            formData.password === formData.confirmPassword
              ? ' .'
              : undefined
          }
          error={errors.password}
        />
      </div>

      {/*   */}
      <div className="hbp:gap-1.5 flex flex-col gap-1">
        <label
          htmlFor="name"
          className="text-body-lg-semibold hbp:text-headline-2xl-semibold text-grey-600"
        >
           <span className="text-red-500">*</span>
        </label>
        <p className="hbp:mb-[7.5px] text-caption-sm-medium hbp:text-body-md-medium text-grey-300 mb-[6px]">
           10  .
        </p>
        <FormInput
          id="name"
          type="text"
          placeholder=" ."
          value={formData.name}
          onChange={handleInputChange('name')}
          successMessage={
            formData.name && !errors.name
              ? '  .'
              : undefined
          }
          error={errors.name}
        />
      </div>

      {/*   */}
      <Button text="" size="xl" disabled={true} />
    </form>
  );
};

export default SignUpForm;
